@import url(https://fonts.googleapis.com/css2?family=Righteous&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;0,700;1,400;1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,
body,
#root {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  background-color: white;
  text-align: center;
  scroll-behavior: smooth;
}

h1, h2 {
  color: #A8E7E9;
  font-family: 'Righteous', cursive;
  -webkit-text-stroke: 1px rgba(121, 116, 116, 0.767);
  font-size: 3.5rem;
}

h3, p, a, li, td, button {
  font-family: 'Poppins', sans-serif;
}

main {
  top: 57px;
}

@media screen and (min-width: 480px) {
  main {
    top: 57px;
  }
}
header {
    display: flex;
    flex-wrap: wrap;
    justify-content: right;
    background-color: #e9e9e9;
    width: 100%;
    position: fixed;
    z-Index: 99;
}

.hamburgerIcon {
    height: 50px;
    width: auto;
    background-color: white;
    border: black 1px solid;
    position: fixed;
    top: 20px;
    right: 20px;
    z-Index: 99;
}

header p {
    margin-left: 60px;
}

header a {
    text-decoration: none;
    color: black;
}

header a:hover{
    color: rgb(185, 185, 185)
}

nav ul {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
}

nav li {
    margin: 0 10px;
    text-align: right;
    list-style: none;
}

li a {
    font-size: 1.5em;
}

@media screen and (min-width: 480px) {
    header {
    -webkit-transform: translateY(0);
            transform: translateY(0);
      position: fixed;
      z-Index: 99;
      justify-content: space-between;
    }

    .hamburgerIcon {
        display: none;
        z-Index: 99;
    }

    nav ul {
        flex-direction: row;
    }

    nav li {
        
        margin: 0 60px;
        text-align: center;
    }

    li a {
        font-size: 1em;
    }


  }
.Banner {
    display: flex;
    align-items: center;
    height: 600px;
    background-color: #1f1f1f;
    box-shadow: 0 10px 10px  darkgrey; 
}

.Title {
    width: 100%;
    position: absolute;
    z-Index: 1;

    text-align: center;
}

.Title h1 {

}

.Banner canvas {
    width: viewport-length;
    height: 100%;
    position: relative;
}


.about {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin-top: 50px;
}

.about div {
    margin: 0 30px 0 30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

/* .aboutInfo {
    display: flex;
    flex-direction: column;
    align
} */

.about img {
    height: 250px;
    -webkit-clip-path: polygon(50% 0%, 80% 10%, 100% 35%, 100% 70%, 80% 90%, 50% 100%, 20% 90%, 0% 70%, 0% 35%, 20% 10%);
            clip-path: polygon(50% 0%, 80% 10%, 100% 35%, 100% 70%, 80% 90%, 50% 100%, 20% 90%, 0% 70%, 0% 35%, 20% 10%);
}

@media screen and (min-width: 480px) {
    .about {
        display: grid;
        grid-template-columns: 1fr 2fr;
    }

    .about img {
        height: 300px;
    }
  }



  

.skill-display {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.skill-card {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    width: 90vh;
    padding: 10px 0 10px 0;
    margin: 20px 40px 20px 40px;
    background-color: #dfdcd0;
    border-radius: 10px;
}

.skill-card > * {
    margin-left:10px;
    margin-right: 10px;
    padding-left:10px;
    padding-right: 10px;
}

.skill-logo {
    max-height: 100px;
    max-width: 120px;
}

@media screen and (min-width: 480px) {
    .skill-card {
        width: 300px;
    }
  }
.Projects {
    position: relative;
    width: 100%;
    text-align: center;
}

.Projects h2 {
    z-Index: 1;
}

.Projects canvas {
    width: 100%;
    height: 80vh; 
}

@media screen and (min-width: 480px) {
    .Projects canvas {
        height: 100vh; 
    }
  }
.ProjectCard {
    position: relative;
    width: 100%;
    z-Index: 2;
    background-color: rgba(255, 220, 154);
 
    padding: 20px 0 20px 0;
    border-bottom: solid black 3px;
    transition: top 250ms ease-in-out;
    overflow: hidden;
}

.ProjectCard h3 {
    color: #A8E7E9;
    -webkit-text-stroke: 1px rgba(121, 116, 116, 0.767);
    font-size: 1.5rem;
    margin-top: 0;
    margin-bottom: 15px;
}

.ProjectCard button {
    margin-bottom: 20px;
    border: none;
    background-color: #FF4E50;
    padding: 10px;
    color: white;
    border-radius: 10px;
}

.ProjectCard button:hover {
    background-color: #ce3f41;
}

.techList {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.techList p {
    margin: 5px;
    padding: 5px;
    border: solid black 1px;
    background-color: rgba(255, 220, 154);
}

@media screen and (min-width: 480px) {
    .ProjectCard button {
        position: absolute;
        right: 20px;
    }

    .ProjectCard {
        position: absolute;
        width: 56%;
        z-Index: 2;
        background-color: rgba(255, 220, 154, 0.76);
        padding: 20px;
        border-bottom-left-radius: 10px;
        border-top-left-radius: 10px;
        border-top: solid black 3px;
        border-bottom: solid black 3px;
        border-left: solid black 3px;
        transition: right 250ms ease-in-out;
        right: 0;
        -webkit-transform: translateY(-90vh);
                transform: translateY(-90vh);
        max-height: 80vh;
        overflow: auto;
        scrollbar-width: thin;
        scrollbar-color  : #FF4E50 grey;
    }

    .ProjectCard::-webkit-scrollbar {
        width: 7px; 
        margin: 3px;
     }
  
    .ProjectCard::-webkit-scrollbar-track {
      background-color: black;
    }
  
    ::-webkit-scrollbar-thumb {
      background: #FF4E50;
      border-radius: 25px;
    }
  }
.Screenshots {
    margin-bottom: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.Screenshots img {
    width: auto;
    height: 200px;
    object-fit: scale-down;
}

.carouselBar {
    width: 100%;
    height: 10px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 10px;
}

.carouselButton {
    height: 100%;
    min-width: 40px;
    margin-left: 5px;
    margin-right: 5px;
    background-color:#FF4E50;
    transition: background-color 250ms ease-in-out;
}

@media screen and (min-width: 480px) {
    .carouselButton {
        min-width: 20px;
        margin-left: 2px;
        margin-right: 2px;
    }

    .Screenshots img {
        width: auto;
        height: 300px;
        object-fit: scale-down;
        padding: 10px;
    }
  }
footer {
    text-align: right;
    background-color: #B1FFFD;
    padding-top: 60px;
    padding-bottom: 30px;
}

footer p {
    margin: 0 20px 0 20px;
}
